/* src/styles/footer.css */


.footer-container {
    width: 100%;
    padding: 20px 40px;
    background: var(--color-fondo-header);
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--color-header-title);
    box-shadow: 0 -4px 10px var(--color-shadow-header);
}

.footer-socials a {
    margin-left: 15px;
    color: var(--color-header-title);
    font-size: 1.2rem;
    transition: color 0.3s ease;
}

.footer-socials a:hover,
.footer-socials a:focus {
    color: var(--color-header-title-highlight);
}
