/* src/styles/searchBar.css */

/* Contenedor general de la barra de búsqueda */
.search-bar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    background: #2e2a45; /* Fondo morado oscuro */
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    width: 100%;
    max-width: 800px;
    margin: 20px auto;
}

/* Estilo para el formulario de búsqueda por nombre */
.name-search-form {
    display: flex;
    align-items: center;
    gap: 10px;
}

.name-search-form input {
    padding: 10px;
    border: none;
    border-radius: 5px;
    outline: none;
    background-color: #fff;
    color: #333;
    transition: box-shadow 0.3s ease;
    font-size: 0.9rem;
    width: 250px;
}

.name-search-form input:focus {
    box-shadow: 0 0 8px rgba(114, 70, 199, 0.5); /* Sombra morada */
}

.name-search-form button {
    padding: 10px 15px;
    background: #e74c3c; /* Rojo */
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    font-weight: bold;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}

.name-search-form button:hover {
    background: #ff6f61; /* Rojo más claro */
    transform: translateY(-2px);
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.3);
}

/* Estilo para el desplegable de búsqueda por ubicación */
.location-search-dropdown {
    display: flex;
    align-items: center;
}

.location-search-dropdown select {
    padding: 10px;
    border: none;
    border-radius: 5px;
    outline: none;
    background-color: #fff;
    color: #333;
    font-size: 0.9rem;
    cursor: pointer;
    transition: box-shadow 0.3s ease, background 0.3s ease;
}

.location-search-dropdown select:focus {
    box-shadow: 0 0 8px rgba(114, 70, 199, 0.5); /* Sombra morada */
    background-color: #f0f0f0;
}

/* Responsividad */
@media (max-width: 768px) {
    .search-bar-container {
        flex-direction: column;
        gap: 15px;
        padding: 15px;
    }

    .name-search-form input,
    .location-search-dropdown select {
        width: 100%;
    }
}
