/* src/styles/header.css */

/* Variables de color */
:root {
    --color-fondo-header: linear-gradient(145deg, #3b2f61, #251c3e); /* Degradado morado */
    --color-header-title: #f8f8f8; /* Blanco suave */
    --color-header-title-highlight: #34d058; /* Verde brillante */
    --color-header-border: #6a4686; /* Morado oscuro */
    --color-shadow-header: rgba(0, 0, 0, 0.3);
    --color-search-bg: rgba(255, 255, 255, 0.15);
    --color-search-bg-hover: rgba(255, 255, 255, 0.3);
    --color-search-input-bg: #fff;
    --color-search-input-focus-bg: #f0f0f0;
    --color-search-btn-bg: #e74c3c; /* Rojo */
    --color-search-btn-hover-bg: #ff6f61; /* Rojo más claro */
    --color-shadow-search-btn: rgba(0, 0, 0, 0.2);
    --color-shadow-search-btn-hover: rgba(0, 0, 0, 0.4);
    --color-error-bg: #e74c3c;
    --color-error-text: #fff;
  }
  
  /* Contenedor general del header */
  .header-container {
    width: 100%;
    padding: 20px 40px;
    background: var(--color-fondo-header);
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 4px 15px var(--color-shadow-header);
    border-bottom: 2px solid var(--color-header-border);
    position: sticky;
    top: 0;
    z-index: 100;
    box-sizing: border-box;
    transition: background 0.3s ease;
  }
  
  .header-container:hover {
    background: linear-gradient(145deg, #4a376b, #332150); /* Fondo morado más brillante al hover */
  }
  
  /* Estilo del título principal */
  .main-title {
    font-size: 2.4rem;
    color: var(--color-header-title);
    font-weight: bold;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    margin: 0;
    transition: color 0.3s ease, text-shadow 0.3s ease;
  }
  
  .main-title span {
    color: var(--color-header-title-highlight); /* Verde para el span */
    text-decoration: underline; /* Subrayado para resaltar */
  }
  
  .main-title:hover {
    color: var(--color-header-title-highlight);
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.6); /* Brillo extra al hover */
  }
  
  /* Botón de menú móvil */
  .mobile-menu-btn {
    display: none;
    background: none;
    border: none;
    color: var(--color-header-title);
    font-size: 1.8rem;
    cursor: pointer;
    transition: color 0.3s ease;
  }
  
  .mobile-menu-btn:hover,
  .mobile-menu-btn:focus {
    color: var(--color-header-title-highlight);
    outline: none;
  }
  
  /* Navegación */
  .nav-links {
    display: flex;
    align-items: center;
  }
  
  .nav-links ul {
    list-style: none;
    display: flex;
    gap: 20px;
  }
  
  .nav-links ul li a {
    color: var(--color-header-title);
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease, text-decoration 0.3s ease;
  }
  
  .nav-links ul li a:hover,
  .nav-links ul li a:focus {
    color: var(--color-header-title-highlight);
    text-decoration: underline;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.5);
  }
  
  /* Formulario de búsqueda */
  .search-form {
    display: flex;
    align-items: center;
    gap: 10px;
    background: var(--color-search-bg);
    padding: 8px 15px;
    border-radius: 25px;
    box-shadow: 0 2px 6px var(--color-shadow-search-btn);
    transition: background 0.3s ease, box-shadow 0.3s ease;
  }
  
  .search-form:hover {
    background: var(--color-search-bg-hover);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  }
  
  .search-input {
    padding: 8px 15px;
    border: none;
    border-radius: 20px;
    outline: none;
    background-color: var(--color-search-input-bg);
    color: #333;
    transition: box-shadow 0.3s ease, background 0.3s ease;
    width: 250px;
    font-size: 0.9rem;
  }
  
  .search-input:focus {
    box-shadow: 0 0 8px rgba(114, 70, 199, 0.5);
    background-color: var(--color-search-input-focus-bg);
  }
  
  .search-btn {
    padding: 8px 15px;
    background: var(--color-search-btn-bg);
    color: var(--color-header-title);
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
    font-weight: bold;
    box-shadow: 0px 4px 6px var(--color-shadow-search-btn);
  }
  
  .search-btn:hover,
  .search-btn:focus {
    background: var(--color-search-btn-hover-bg);
    transform: translateY(-2px);
    box-shadow: 0px 6px 12px var(--color-shadow-search-btn-hover);
    outline: none;
  }
  
  /* Contenedor de usuario y bienvenida */
  .header-user {
    color: var(--color-header-title-highlight);
    font-size: 1rem;
    font-weight: bold;
  }
  
  /* Mensaje de error */
  .error-message {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--color-error-bg);
    color: var(--color-error-text);
    padding: 8px 15px;
    border-radius: 5px;
    box-shadow: 0px 4px 6px var(--color-shadow-header);
    font-size: 0.9rem;
    margin-top: 5px;
    animation: fadeInOut 3s forwards;
    text-align: center;
    z-index: 99;
  }
  
  @keyframes fadeInOut {
    0% { opacity: 0; }
    10% { opacity: 1; }
    90% { opacity: 1; }
    100% { opacity: 0; }
  }
  
  /* Menú móvil */
  @media (max-width: 768px) {
    .mobile-menu-btn {
      display: block;
    }
  
    .nav-links {
      position: absolute;
      top: 70px;
      left: 0;
      width: 100%;
      background: var(--color-fondo-header);
      flex-direction: column;
      align-items: center;
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.3s ease;
    }
  
    .nav-links.active {
      max-height: 300px;
    }
  
    .nav-links ul {
      flex-direction: column;
      gap: 15px;
      padding: 20px 0;
    }
  
    .search-form {
      width: 90%;
      margin-top: 15px;
    }
  
    .search-input {
      width: 200px;
    }
  }
  
  @media (max-width: 480px) {
    .search-input {
      width: 150px;
    }
  }
  